import config from '../../components/config/config.vue'
export default {
	onLoad() {
		//加载用户信息
		this.onloaduser()	
	},
	// mounted() {
	// 	//加载用户信息
	// 	this.onloaduser()									
	// },
	data() {
		return {
			userimg:"../../static/nickname.png",				
			id:"0",
			user_name:"Nil",
			nickname:"Nil",
			phone:"Nil",
			createtime:"",
	}
	},
	methods:{
		onloaduser(){
			// config.txtshow('网络异常')
			  var value = uni.getStorageSync('token');
			  if(value){
			  	uni.request({
			  	    url: config.httpUrl +'/api/v1/user/me',
			  		method:"GET",
			  	    header: {
			  	       'content-type': 'application/x-www-form-urlencoded',
			  		   'Authorization' : value,
			  	    },
			  	    success: (res) => {
			  			var jsondata=res.data
			  			if(jsondata.code=='0'){
			  				this.nickname=jsondata.data.nickname
							if(jsondata.data.avatar){
								this.userimg=jsondata.data.avatar
							}
							this.id=jsondata.data.id
							this.user_name=jsondata.data.user_name
							this.phone=jsondata.data.phone
							var date=new Date(jsondata.data.created_at*1000)
							var Y = date.getFullYear() + '-'
							var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-'
							var D = (date.getDate()< 10 ? '0'+date.getDate() :date.getDate() )+ ' '
							this.createtime=Y+M+D
							
			  			}else{
			  				 config.txtshow(jsondata.msg)				  				
			  			}								
			  	    },
			  		fail: () =>{  
			  			 config.txtshow('网络异常')
			  	     }, 
			  	});
			  }else{
			  	//跳转到登录界面
			  	uni.redirectTo({
			      url: '../../pages/user/login'
			  });
			    
			}
		},
		goback(){
			uni.switchTab({
				url: '../show/me'
			})
		},
		quit(){
			uni.showModal({
			    title: '提示',
			    content: '确定要退出登陆么？',
			    success: function (res) {
			        if (res.confirm) {
			            //console.log('用户点击确定');
						uni.removeStorageSync('token');
						uni.closeSocket();
						uni.navigateTo({
						    url: 'login'
						});
			        } else if (res.cancel) {
			            //console.log('用户点击取消');
			        }
			    }
			});
		},
		
		update_avatar(){
			uni.redirectTo({
			    url: 'choose'
			});
			
			// // 从相册选择6张图
			// uni.chooseImage({
			//     count: 1,
			//     sizeType: ['original', 'compressed'],
			//     sourceType: ['album'],
			//     success: function(res) {
			//         console.log(JSON.stringify(res.tempFiles));
			       
			//     }
			//     });
			
		},	

	},
	
}
